@import url("https://fonts.googleapis.com/css2?family=Questrial&display=swap");

* {
  font-family: Questrial;
  box-sizing: border-box;
}

textarea {
  font-family: initial;
}

body {
  background: #76b9b0;
  margin: 2% 3%;
}

h1 {
  margin-bottom: 20px;
}

h4 {
  border: solid rgb(150, 150, 150) 1px;
  background: black;
  color: #76b9b0;
  border-top-right-radius: 5px;
  border-top-left-radius: 5px;
  margin-bottom: 0;
  border-bottom: 0;
  padding: 5px;
}

.form-control,
.preview {
  min-height: 400px;
  background: white;
  overflow: auto;
}

.preview img {
  max-width: 20%;
}

@media (max-width: 400px) {
  .preview-container {
    margin-top: 10px;
  }
}
